import configureMockStore from 'redux-mock-store';
import thunk from 'redux-thunk';
import fetchMock from 'fetch-mock';
import {
  REQUEST_UPCOMING_MOVIES_ACTION,
  REQUEST_UPCOMING_MOVIES_SUCCESS_ACTION,
} from '../src/types/types';
import {fetchUpcomingMovies} from '../src/action';

const middlewares = [thunk];
const mockStore = configureMockStore(middlewares);

describe('async actions', () => {
  describe('request movie action and request success action', () => {
    afterEach(() => {
      fetchMock.restore();
    });

    it('requesting movies', () => {
      fetchMock.getOnce(
        'https://api.themoviedb.org/3/movie/upcoming?api_key=xyz',
        {
          body: {results: []},
          headers: {'content-type': 'application/json'},
        },
      );

      const expectedActions = [
        {type: REQUEST_UPCOMING_MOVIES_ACTION},
        {type: REQUEST_UPCOMING_MOVIES_SUCCESS_ACTION, data: []},
      ];

      const store = mockStore({movies: []});

      return store
        .dispatch(fetchUpcomingMovies())
        .then(() => expect(store.getActions()).toEqual(expectedActions));
    });
  });
});
